export default {
	components: {
	},
	props: [
		'isSelect',
		'iconSrc',
		'iconWidth',
		'iconHeight',
		'iconTAlign',
		'iconPadding'
		],
	data() {
		return {
			imgSrc: this.iconSrc.notSelected,
		};
	},
	created() {},
	iconSrc: {
		value: {
			handler(a, b) {
				this.imgSrc = a;
			},
			deep: true
		}
	},
	methods: {
		// 点击替换图片
		replaceIcon() {
			console.log('点击替换图片');
			this.imgSrc = this.iconSrc.selected; // 点击切换样式
			this.$emit('tap-ReplaceIcon');
		},
	}
};
